nav{
    display: flex;
    border-bottom: 2px solid #f3f3f3;
    justify-content: space-around;
    align-items: center;
    padding: 5px;
    z-index: 999;
    
}
.logo-container img{
    width: 10rem;
    box-shadow: #b13636;
}

.search-input{
    padding: 12px 20px;
    border: none;
    background: #f7f6f6;
    outline: none;
    margin-right: 20px;
    border-radius: 5px;
    position: relative;
    width: 30rem;
}
.nav-icons{
    width: 1.5rem; 
    height: 1.5rem;
    margin-left: 2rem;
    color:#000 ;
}